:root {
  --font-family-main: "DM Sans", sans-serif;
  --font-family-title: "Titan One", cursive;
  --cl-txt-main: #000;
  --cl-txt-second: #907e82;
  --cl-txt-white: #fff;
  --cl-txt-light: #e1e1e1;
  --cl-txt-accent-light: #ffa5ba;
  --cl-txt-accent-dark: #d41443;
  --bgc-main: #fff;
  --bgc-second: #fafafa;
  --bgc-menu: #e17992;
  --bgc-accent-ice: #fff5f6;
  --bgc-accent-cream: #ffb8ca;
  --bgc-accent-coffee: #f0d1a5;
  --bgc-accent-shake: #c2e297;
  --bgc-hover: #ecebeb;
  --anim: 250ms cubic-bezier(.4, 0, .2, 1);
  --slow-anim: 200ms ease;
  --fast-anim: 400ms ease-in-out;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family-main);
  letter-spacing: .04em;
  color: var(--cl-txt-main);
  background-color: var(--bgc-main);
  font-size: 14px;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  color: inherit;
  transition: color var(--anim);
  text-decoration: none;
}

.link:hover, .link:focus {
  color: var(--cl-txt-accent-dark);
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.section {
  width: 100%;
  margin: 0 auto;
  padding: 60px 0;
}

.uppertitle {
  font-family: var(--font-family-title);
  text-transform: uppercase;
  color: var(--cl-txt-accent-light);
  align-items: center;
  margin-bottom: 15px;
  font-size: 30px;
  line-height: 1.13;
  display: flex;
}

.section-title {
  font-family: var(--font-family-title);
  text-transform: uppercase;
  color: var(--cl-txt-accent-dark);
  font-size: 34px;
  line-height: 1.15;
}

.description {
  letter-spacing: .02em;
  margin-bottom: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.62;
}

.no-scroll {
  overflow: hidden;
}

.js-no-transparency {
  transition: background-color var(--slow-anim), backdrop-filter var(--slow-anim);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(225, 121, 145, .4);
}

@keyframes slide-top {
  from {
    transform: translateY(-5px);
  }

  to {
    transform: translateY(5px);
  }
}

.btn {
  min-width: 160px;
  max-width: 190px;
  height: 44px;
  cursor: pointer;
  color: var(--cl-txt-accent-dark);
  background-color: var(--cl-txt-white);
  text-align: center;
  transition: color var(--anim), background-color var(--anim);
  border: none;
  border-radius: 22px;
  outline: none;
  padding: 0;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.btn:hover, .btn:focus {
  background-color: var(--bgc-hover);
}

.btn:after {
  content: "";
  width: 9px;
  height: 9px;
  background-image: url("pointer-red.bedfb617.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 13px;
  display: inline-block;
}

.btn--mod-red {
  color: var(--cl-txt-white);
  background-color: var(--cl-txt-accent-dark);
}

.btn--mod-red:after {
  content: "";
  width: 9px;
  height: 9px;
  background-image: url("pointer-white.cefefed9.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 13px;
  display: inline-block;
}

.btn--mod-red:hover, .btn--mod-red:focus {
  background-color: var(--bgc-hover);
  color: var(--cl-txt-accent-dark);
}

.btn--mod-red:hover, .btn--mod-red:focus .btn--mod-red:after {
  background-image: url("pointer-red.bedfb617.svg");
}

.social {
  flex-direction: row;
  display: flex;
}

.social__item + .social__item {
  margin-left: 28px;
}

.social__item {
  width: 34px;
  height: 34px;
}

.social__icon {
  max-width: 19px;
  max-height: 19px;
  fill: var(--cl-txt-white);
}

.social__link {
  height: 100%;
  background-color: var(--cl-txt-accent-light);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.social__link:hover, .social__link:focus {
  background-color: var(--cl-txt-accent-dark);
  outline: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header__mobile-menu {
  z-index: 11;
  width: 250px;
  height: 100vh;
  transition: transform var(--slow-anim);
  background-color: var(--bgc-menu);
  visibility: hidden;
  flex-direction: column;
  padding-top: 60px;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: scroll;
  transform: translateX(100%);
}

.header__mobile-menu.is-open {
  visibility: visible;
  transition: transform var(--slow-anim);
  transform: translateX(0);
}

@media screen and (min-width: 768px) {
  .header__mobile-menu {
    width: 255px;
    padding-top: 84px;
  }
}

@media screen and (min-width: 1200px) {
  .header__mobile-menu {
    display: none;
  }
}

.header__mobile-menu .btn {
  margin-bottom: 32px;
}

.mobile-btn {
  z-index: 11;
  width: 20px;
  height: 20px;
  color: var(--cl-txt-accent-dark);
  cursor: pointer;
  transition: color var(--anim);
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-btn:hover, .mobile-btn:focus {
  color: var(--cl-txt-white);
}

.mobile-btn.is-open {
  position: fixed;
  overflow-y: scroll;
}

@media screen and (min-width: 768px) {
  .mobile-btn {
    top: 40px;
  }
}

@media screen and (min-width: 1200px) {
  .mobile-btn {
    display: none;
  }
}

.mobile-btn__icon {
  fill: currentColor;
}

.mobile-btn__icon-open {
  display: block;
}

.mobile-btn.is-open .mobile-btn__icon-open, .mobile-btn__icon-close {
  display: none;
}

.mobile-btn.is-open .mobile-btn__icon-close {
  display: block;
  position: fixed;
}

.mobile-menu__navigation .navigation__list {
  display: inline-block;
}

.mobile-menu__navigation .navigation__item {
  margin-right: 0;
}

.mobile-menu__navigation .navigation__item:not(:last-child) {
  margin-bottom: 24px;
}

.mobile-menu__navigation .navigation__link {
  transition: color var(--anim);
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
  display: flex;
  position: relative;
}

.mobile-menu__navigation .navigation__link:after {
  content: " ";
  height: 1px;
  width: 250px;
  background-color: #e18298;
  position: absolute;
  bottom: -11px;
  left: -20px;
}

.mobile-menu__navigation .navigation__link--current {
  color: var(--cl-txt-accent-dark);
}

.mobile-menu__navigation .navigation__link--current:before {
  content: " ";
  height: 1px;
  width: 250px;
  background-color: #e18298;
  position: absolute;
  top: -11px;
  left: -20px;
}

.modal {
  width: calc(100vw - 30px);
  max-height: calc(100vh - 30px);
  background: var(--bgc-main);
  opacity: 1;
  transition: transform var(--slow-anim), visibility var(--slow-anim), opacity var(--slow-anim);
  border-radius: 10px;
  padding: 40px 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.backdrop.is-hidden > .modal {
  transform: translate(-50%, -50%)scale(1.5);
}

@media screen and (min-width: 320px) {
  .modal {
    width: 300px;
    transform: translate(-50%, -50%)scale(1);
  }
}

@media screen and (min-width: 768px) {
  .modal {
    width: 450px;
    padding: 40px 30px;
    position: absolute;
    top: 50%;
    left: 50%;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 540px;
    min-height: 600px;
    padding: 40px;
  }
}

.modal__title {
  text-align: center;
  margin-bottom: 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.13;
}

@media screen and (min-width: 768px) {
  .modal__title {
    font-size: 20px;
    font-weight: 700;
    line-height: 1.15;
  }
}

.modal__btn-close {
  width: 25px;
  height: 25px;
  color: var(--cl-txt-main);
  background: var(--bgc-main);
  cursor: pointer;
  transition: color var(--slow-anim);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal__btn-close:hover, .modal__btn-close:focus {
  color: var(--cl-txt-accent-dark);
}

@media screen and (min-width: 768px) {
  .modal__btn-close {
    width: 30px;
    height: 30px;
  }
}

.modal__icon {
  fill: currentColor;
}

.form {
  text-align: center;
  display: block;
}

.form__field {
  text-align: left;
  margin-bottom: 10px;
  display: block;
}

.form__label {
  letter-spacing: .01em;
  color: var(--cl-txt-main);
  margin-bottom: 4px;
  font-size: 12px;
  line-height: 1.17;
  display: inline-block;
}

.form__wrapper {
  display: block;
  position: relative;
}

.form__input {
  width: 100%;
  height: 40px;
  color: var(--cl-txt-main);
  transition: border-color var(--slow-anim);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  padding: 12px 16px 12px 42px;
  display: flex;
}

.form__field:focus-within .form__input {
  border-color: var(--cl-txt-accent-dark);
  outline: 0;
}

.form__icon {
  fill: var(--cl-txt-second);
  transition: fill var(--slow-anim);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.form__field:focus-within .form__icon {
  fill: var(--cl-txt-accent-dark);
}

.form__field-comment {
  margin-bottom: 20px;
}

.form__comment {
  width: 100%;
  height: 100px;
  resize: none;
  letter-spacing: .01em;
  color: var(--cl-txt-main);
  transition: border-color var(--slow-anim);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  padding: 12px 16px;
  line-height: 1.14;
  display: flex;
}

.form__comment:hover, .form__comment:focus, .form__comment:active {
  border-color: var(--cl-txt-accent-dark);
  outline: 0;
}

.form__comment::-ms-input-placeholder {
  color: rgba(144, 126, 130, .7);
}

.form__comment::placeholder {
  color: rgba(144, 126, 130, .7);
}

@media screen and (min-width: 768px) {
  .form__comment {
    height: 120px;
  }
}

.form__field-checkbox {
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
}

.form__wrapper-checkbox {
  width: 16px;
  height: 15px;
  cursor: pointer;
  margin-right: 7px;
  display: block;
  position: relative;
}

.form__icon--checked {
  opacity: 0;
  transition: opacity var(--slow-anim);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.form__input-checkbox:checked + .form__icon--checked {
  opacity: 1;
}

.form__icon--unchecked {
  width: 16px;
  height: 15px;
  border: 2px solid var(--cl-txt-main);
  transition: border-color var(--slow-anim);
  border-radius: 2px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.form__input-checkbox:checked ~ .form__icon--unchecked, .form__input-checkbox:focus ~ .form__icon--unchecked, .form__input-checkbox:hover ~ .form__icon--unchecked {
  border-color: var(--cl-txt-accent-dark);
}

.form__label-checkbox {
  color: var(--cl-txt-main);
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin-bottom: 0;
  font-size: 12px;
  line-height: 1.17;
}

@media screen and (min-width: 1200px) {
  .form__label-checkbox {
    font-size: 14px;
    line-height: 1.71;
  }
}

.form__agreement {
  color: var(--cl-txt-accent-dark);
}

.form__btn {
  padding: 10px 55px;
}

.backdrop {
  z-index: 15;
  width: 100%;
  height: 100%;
  transition: opacity var(--anim), visibility var(--anim), transform var(--anim);
  background: rgba(255, 245, 246, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.scroll-to-top {
  width: 25px;
  height: 25px;
  background-color: var(--cl-txt-accent-dark);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: slide-top 1s linear 2s infinite alternate both;
  display: flex;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .scroll-to-top {
    width: 30px;
    height: 30px;
  }
}

.arrow-top {
  fill: var(--cl-txt-white);
  width: 10px;
  height: 10px;
}

@media screen and (min-width: 768px) {
  .arrow-top {
    width: 15px;
    height: 15px;
  }
}

.header {
  width: 100%;
  z-index: 10;
  position: fixed;
  top: 0;
}

.header.js-no-transparency {
  background-color: rgba(255, 184, 202, .75);
}

.header.js-no-transparency .mobile-btn {
