    top: 40px;
  }
}

.header__container {
  text-align: left;
  align-content: center;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 320px) {
  .header__container {
    padding-top: 32px;
    padding-bottom: 32px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .header__container {
    height: 85px;
    justify-content: space-around;
    padding-bottom: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .header__container {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.logo {
  position: absolute;
  top: 8px;
  left: 20px;
}

@media screen and (min-width: 768px) {
  .logo {
    top: 32px;
    left: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .logo {
    top: 35px;
    left: 40px;
  }
}

.navigation {
  margin-left: 200px;
}

.navigation__list {
  display: none;
}

@media screen and (min-width: 1200px) {
  .navigation__list {
    flex-wrap: wrap;
    justify-content: center;
