}

.navigation__link {
  color: var(--cl-txt-white);
  align-items: center;
  padding-top: 52px;
  padding-bottom: 52px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.navigation__link--current {
  color: var(--cl-txt-accent-dark);
}

.header__btn {
  display: none;
}

@media screen and (min-width: 768px) {
  .header__btn {
    z-index: 11;
    display: inline-block;
    position: absolute;
    top: 31px;
    right: 83px;
  }
}

@media screen and (min-width: 1200px) {
  .header__btn {
    position: static;
  }
}

